import React, { Component } from 'react';
import { Header } from './Header';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import axios from 'axios';
import Container from 'react-bootstrap/Container';
import Form  from 'react-bootstrap/Form';
import Loader from './loader/loader';
import Footer from './Footer/Footer';
import Tabs from './tabs/tabs';
import Button from 'react-bootstrap/Button'

import {
    BrowserRouter as Router,
    Route,
    Link,
    Redirect,
    withRouter
} from "react-router-dom";
class SelecetAll extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        invalidUrl:'',
        text:'Check All'
      }
      componentDidMount() {
      
            
       
      }
      checkAllAssets = () =>{

        var items=document.getElementsByName("filterDataPanel");
        for(var i=0; i<items.length; i++){
            if(items[i].type=='checkbox'){
                items[i].checked=true;

            }

        }
        this.setState({ text:'Uncheck All' }); 
        global.SelectAllAssetWins=true;


      }
      uncheckAllAssets = () =>{

        var items=document.getElementsByName("filterDataPanel");
        for(var i=0; i<items.length; i++){
            if(items[i].type=='checkbox')
                items[i].checked=false;
        }
        this.setState({ text:'Check All' }); 
        global.SelectAllAssetWins=undefined;

        

      }
  render() {
   

        return (
            <>
              {( (this.state.text=="Check All")) ? (

                <a class="selectAll btn btn-primary btn-sm" href="javascript:void(0)" onClick={(e) => this.checkAllAssets()} >{this.state.text}</a>
              ):(
                <a class="selectAll btn btn-primary btn-sm" href="javascript:void(0)" onClick={(e) => this.uncheckAllAssets()}>{this.state.text}</a>
              )}
            </>
               
        );
    }
  }
  
  export default SelecetAll;